<template>
    <Modal 
      :modelValue="modelValue" 
      :title="title"
      @update:modelValue="$emit('update:modelValue', $event)"
      @close="closeModal"
    >
    <template v-if="loading">
        <div class="w-full h-40 flex items-center justify-center">
            <Loading />
        </div>
    </template>
    <template v-else>
        <div class="flex gap-2 py-4 px-6 rounded-lg" :class="data?.type_notification == 'failure' ? 'bg-orange-lighten text-orange-03' : 'bg-blue-lighten text-blue-03'">
            <i class="material-icons">info</i>
            <div v-html="data?.content" class="leading-6"></div>
        </div>
        <div v-if="!isHideAddtionalInfo" class="mt-6">
            <p class="font-semibold">This issue with pricing accuracy is affecting the following categories/items:</p>
            <div class="mt-2 py-2 px-4 rounded-lg border border-grey-03 max-h-72 overflow-y-auto">
                <ul class="list-disc pl-5 space-y-2">
                    <li v-for="item in data?.addtional_info?.data" :key="item">
                        {{ item }}
                    </li>
                </ul>
            </div>
        </div>
    </template>
        
        <template #footer>
            <div class="flex justify-between">
                <Button @click="closeModal" color="teal" view="smooth">Close</Button>
                <a v-if="data?.direct_to" :href="data?.direct_to">
                    <Button color="teal">{{ data?.category_notification == "issue-pricing-source" && "Go to Price Editor" }}</Button>
                </a>
            </div>
        </template>
    </Modal>
  </template>
  
  <script setup>
  import { defineProps, defineEmits, computed } from 'vue'
  import Modal from 'bbUi/components/modal/modal.vue'
  import Button from 'bbUi/components/button/button.vue'
  import Loading from 'bbUi/components/loading/loading.vue'
  
  const props = defineProps({
    modelValue: {
      type: Boolean,
      required: true
    },
    title: {
      type: String,
      default: 'Message Info'
    },
    data: {
      type: Object,
      default: () => ({})
    },
    loading: {
      type: Boolean,
      default: false
    }
  })
  
  const emit = defineEmits(['update:modelValue', 'onClose'])
  
  const closeModal = () => {
    handleCloseAction()
    emit('update:modelValue', false)
  }
  
  const handleCloseAction = () => {
    emit('onClose')
  }

  const isHideAddtionalInfo = computed(() => {
    return !props.data?.addtional_info || props.data?.addtional_info == 'null' || props.data?.addtional_info?.data?.length == 0
  })
  </script>